import React, {Component} from 'react';

class UserCode extends Component{
    render(){
        return (
            <div>
                <p>代码仓库</p>
                <p>GIT 仓库</p>
                <p>codes：{this.props.codes}</p>
            </div>
        )
    }
}

export default UserCode;


