import React, {Component} from 'react';

class UserInfo extends Component{
    render(){
        return (
            <div>
                <p>基本信息</p>
                <p>姓名：{this.props.username}</p>
                <p>介绍：{this.props.userInfo.username}</p>
            </div>
        )
    }
}

export default UserInfo;


